<script>
   /**************************************************************/
   /* PCVCalculator                                              */
   /* -------------------------                                  */
   /* component for computing a PV-set                           */
   /**************************************************************/

   import { Select, Container, Button, Range, Switch, Spinner, ButtonCancel, ButtonDownload } from 'svelte-controls-basic';
   import { pca_fit, pcvpca } from '../../../shared/wasm/dist/pca.js';
   import { downloadData  } from '../../../shared/utils.js';

   // main properties of the whole app
   let {
      WASM,
      calset,
      outliers = [],
      pvset = $bindable(),
   } = $props()

   // error handling parameetrs
   let status = $state('neutral');
   let errMsg = $state('');

   // PV-set calculation parameters
   let ncomp = $state(1);
   let center = $state(true);
   let scale = $state(false);

   let cvtypeOptions = $state(['ven', 'rand', 'loo']);
   let cvType = $state('ven');

   // promise container for calculator
   let promise = $state();

   // compute number of segments options based on dataset size
   let n = calset.values.nrows - outliers.length;
   let nsegOptions = calset && calset.values.nrows > 0 ?
      [3, 4, 5, 10, 20].filter(v =>
         v >= (n / (n - ncomp - 1)) && v <= n / 2
      ) : [];

   // select first available option for number of segments as initial
   let nseg = $state(nsegOptions.length > 0 ? nsegOptions[0] : 0);

   // if no options available (dataset is too small) - limit CV choice by LOO
   $effect(() => {
      if (nsegOptions.length === 0) {
         cvtypeOptions = ['loo'];
         cvType = 'loo';
      }
   });

   function removeOutliers(arr, outliers) {
      if (outliers.length === 0) return arr;
      return calset.classes.filter((i, v) => !outliers.includes(i));
   }

   // function that computes PV-set using given parameters
   async function computePVSet(calset, cv) {
      promise = WASM()
         .then(Module => {
            const cv_type = cv.type == 'loo' ? 0 : cv.type == 'rand' ? 2 : 1;
            const model = pca_fit(Module, calset.values, ncomp, center, scale, outliers);
            const pvvalues = pcvpca(Module, model, calset.values, ncomp, cv.nseg, cv_type, 0, outliers);


            pvset = {

               classLabelsVarname: calset.classLabelsVarname,
               classNames: calset.classNames,
               dec: calset.dec,
               fileName: calset.fileName + '-pcv',
               hasClassNames: calset.hasClassNames,
               hasLabels: calset.hasLabels,
               header: calset.header,
               labelsVarname: calset.labelsVarname,

               classes: removeOutliers(calset.classes, outliers),
               objindices: removeOutliers(calset.objindices, outliers),
               objlabels: removeOutliers(calset.objlabels, outliers),

               sep: calset.sep,
               values: pvvalues,
               varAxisTitle: calset.varAxisTitle,

               varindices: calset.varindices,
               varlabels: calset.varlabels,
               varrev: calset.varrev,
               varvalues: calset.varvalues,
               varvaluesName: calset.varvaluesName,
               varvaluesUnits: calset.varvaluesUnits,
            }
         })
         .catch((e) => {
            console.error(e)
            pvset = null;
            status = 'error';
            errMsg = e;
         });
   }

   // event handler which triggers the model computation
   function compute(e) {
      status = 'neutral';
      errMsg = '';
      computePVSet(calset, {type: cvType, nseg: nseg});
   }

   // function which resets the state of the component
   function reset() {
      status = 'neutral';
      errMsg = '';
      promise = null;
      pvset = null;
   }

   // set initial number of components depending on dataset size and manual limit of A = 20
   let maxNComp = $derived(calset ? Math.min(calset.values.nrows - 2 - outliers.length, calset.values.ncols - 1, 50) : null);


   // if data file is empty reset status and error messages
   $effect(() => {
      if (calset === null) reset();
   })

   $effect(() => {
      ncomp = maxNComp ? Math.min(maxNComp, 20) : null;
   });
</script>


{#if (!pvset) && status !== 'busy'}

   <Container name="ncomp" label="# of PCs">
      <Range id="ncomp" bind:value={ncomp} min={1} max={maxNComp} step={1} decNum={0} />
   </Container>

   <Container name="center" label="Center">
      <Switch id="center" bind:value={center} options={["no", "yes"]} />
   </Container>

   <Container name="scale" label="Scale">
      <Switch id="scale" bind:value={scale} options={["no", "yes"]} />
   </Container>

   <Container name="split" label='Split type:'>
      <Select id='cvType' bind:value={cvType} options={cvtypeOptions} />
   </Container>

   {#if cvType !== 'loo'}
   <Container name="nseg" label='Segments:'>
      <Select id='nseg' bind:value={nseg} options={nsegOptions} />
   </Container>
   {/if}

   <Container name="run-button" label=' '>
      <Button onclick={compute} text="Compute PV-set" />
   </Container>
{/if}

{#if promise}

   {#await promise}
      <Container name="spinner">
         <Spinner />
         <p>Computing PV-set...</p>
      </Container>
   {:catch error}
      <Container name="error" status="error">
         <p>{error}</p>
      </Container>
   {/await}

{/if}

{#if pvset }
<Container name="info">
   <p><span style="color:#a0a0a0;">PV-set:</span> {pvset.values.nrows} &times; {pvset.values.ncols}, {cvType}
      {cvType !== 'loo' ? nseg + ' seg' : ''}</p>
   <ButtonDownload onclick={() => downloadData(pvset, calset.fileName + '-pv')} />
   <ButtonCancel onclick={reset} />
</Container>
{/if}

{#if status === 'error' && errMsg}
   <Container name="error" status='error'>
      <p>{errMsg}</p>
   </Container>
{/if}

